<template>
	<div class="card" style="width: 23rem">
		<div class="card-body">
			<h5 class="card-title">object structure</h5>
			<div v-for="(item, index) in propertyNames" class="plekjeslist">
				<div v-if="typeof(the_object[item])=='object'">
                    {{ item }} size: {{ the_object[item].length }}
                </div>
				<div v-else>{{ item }}: {{ the_object[item] }}</div>
			</div>
		</div>
	</div>
</template>

<script>
export default {
	props: ["the_object"],
	data() {
		return {
			propertyNames: [],
		};
	},
	methods: {
		getProperties() {
			this.propertyNames = Object.getOwnPropertyNames(this.the_object);
		},
	},
	watch: {
		the_object: function (val) {
			this.getProperties();
		},
	},
};
// get properties of object
// for each property, display value
// for lists loops, recursively go deeper
</script>

