<template>
	<div id="mapContainer"></div>
</template>

<script>
import "leaflet/dist/leaflet.css";
import L from "leaflet";

export default {
	props: ["featcol"],
	name: "LeafletMap",
	data() {
		return {
			center: [51.11, 4.46],
			markerIcon: L.icon({
				iconUrl: require("../assets/logo.png"),
				iconSize: [34, 34],
			}),
			osm_layer: L.tileLayer(
				"https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
			),
			layercontrol: L.control.layers(),
		};
	},
	methods: {
		setupLeafletMap() {
			// init map
			this.map = L.map("mapContainer", {
				center: this.center,
				crs: L.CRS.EPSG3857,
				zoom: 11,
				zoomControl: true,
				preferCanvas: false,
			});

			// add osm tile layer
			this.osm_layer.addTo(this.map);

			// add scale
			L.control.scale().addTo(this.map);

			// add layer control
			this.layercontrol.addTo(this.map);
			this.layercontrol.addBaseLayer(this.osm_layer, "OSM layer");

			// add feature group
			//let drawnItems = L.featureGroup().addTo(this.map);
			//this.layercontrol.addOverlay(drawnItems, "drawn items");
		},
	},
	mounted() {
		this.setupLeafletMap();

		//L.geoJSON(data,{onEachFeature: addToolTip}).addTo(this.map)
		//console.log(data)
	},
	beforeUnmount() {
		if (this.map) {
			this.map.remove();
		}
	},
	watch: {
		featcol: function (val) {
			// to check if layer not already on map
			let layerNames = this.layercontrol._layers.map((e) => e.name);

			function addToolTip(feature, layer) {
				if (feature.properties && feature.properties.name) {
					layer.bindTooltip(feature.properties.name);
				}
			}
			var polygonMarkerOptions = {
				radius: 2,
				fillColor: "green",
				color: "green",
				weight: 2,
				opacity: 0.5,
				fillOpacity: 0.2,
			};
			var pointMarkerOptions = {
				radius: 3,
				fillColor: "red",
				color: "red",
				weight: 3,
				opacity: 1,
				fillOpacity: 0.2,
			};
			if (!layerNames.includes(this.featcol.name)) {
				let new_geojson_feature = L.featureGroup().addTo(this.map);
				this.featcol.features.forEach((f) => {
					//console.log(f.geometry.type)
					switch (f.geometry.type) {
						case "Point":
							let icon = this.markerIcon;
							L.geoJSON(f, {
								pointToLayer: function (feature, latlng) {
									//return L.marker(latlng, { icon: icon });
									return L.circleMarker(
										latlng,
										pointMarkerOptions
									);
								},
								onEachFeature: addToolTip,
							}).addTo(new_geojson_feature);
							break;
						case "Polygon":
						case "MultiPolygon":
							L.geoJSON(f, {
								style: polygonMarkerOptions,
								onEachFeature: addToolTip,
							}).addTo(new_geojson_feature);
							break;
						default:
							L.geoJSON(f, {
								onEachFeature: addToolTip,
							}).addTo(new_geojson_feature);
					}
				});

				this.layercontrol.addOverlay(
					new_geojson_feature,
					this.featcol.name
				);
			}
		},
	},
};
</script>

<style scoped>
#mapContainer {
	width: 100vw;
	height: calc(100% - 48px);
	position: absolute;
	top: 48px;
}
#menubutton {
	margin-top: 10px;
	margin-left: 50px;
	position: relative;
	z-index: 9999;
	pointer-events: auto;
}
</style>
