<template>
	<Sidebar
		v-bind:title="title"
		v-bind:subtitle="subtitle"
		@unselectMenuButton="this.$emit('unselectMenuButton')"
	>
		<!-- <ObjectProperties v-bind:the_object="plekollection"></ObjectProperties> -->
		<div class="card" style="width: 23rem">
			<div class="card-body" id="offcanvasExampleLabel">
				<h5 class="card-title">Alle plekollections</h5>
				<div
					v-for="(item, index) in list_of_plekollections"
					class="plekjeslist"
				>
					<div
						@click="getPlekollection(item.name)"
						v-bind:class="{ selected: item.selected }"
					>
						{{ item.name }}
					</div>
				</div>
			</div>
		</div>
		<div v-if="plekollection.name.length != 0">
			<div class="card" style="width: 23rem">
				<div class="card-body" id="offcanvasExampleLabel">
					<h5 class="card-title">
						{{ plekollection.name }}
						<button
							class="btn"
							@click="showPlekollectionOnMap();"
							data-bs-toggle="offcanvas"
							data-bs-target="#offcanvasExample"
							aria-controls="offcanvasExample"
						>
							Showtime
						</button>
					</h5>
					<div
						v-for="(item, index) in plekollection.plekjes"
						class="plekjeslist"
					>
						<div
							@click="getPlekje(item.name)"
							v-bind:class="{ selected: item.selected }"
						>
							{{ item.name }}
						</div>
					</div>
				</div>
			</div>
		</div>
		<div v-if="plekje.name.length != 0">
			<div class="card" style="width: 23rem">
				<div class="card-body" id="offcanvasExampleLabel">
					<h5 class="card-title">{{ plekje.name }}</h5>
					<ul>
						<li>id: {{ plekje._id }}</li>
						<!-- <li>type: {{ plekje.geometry}}</li> -->
						<li>type: {{ plekje.geometry.type }}</li>
						<li>size: {{ plekje.geometry.coordinates.length }}</li>
						<li>tags: {{ plekje.tags }}</li>
					</ul>
					<button type="button" class="btn" @click="showOnMap()">
						Show On Map
					</button>
				</div>
			</div>
		</div>
	</Sidebar>
</template>

<script>
import Sidebar from "./Sidebar.vue";
import ObjectProperties from "./ObjectProperties.vue";
var api = require("../api");

export default {
	data() {
		return {
			title: "Plekjes",
			subtitle: "V0.0.3 created by Tijs Van den Heuvel",
			list_of_plekjes_names: [],
			list_of_all_plekjes: [],
			plekje: {
				_id: "",
				name: "",
				geometry: {},
				tags: [],
			},
			list_of_plekollections: [],
			plekollection: {
				name: "",
				plekjes: [],
			},
			list_of_plekjes_to_display: [],
		};
	},
	components: {
		Sidebar,
		ObjectProperties,
	},
	methods: {
		initPlekjesList() {
			api.apiCall(
				"GET",
				"http://127.0.0.1:3000/plekjes/get_all",
				null,
				(e) => {
					let mongodata = JSON.parse(e);
					this.list_of_all_plekjes = mongodata;
					this.list_of_plekjes_names = mongodata.map((x) => ({
						name: x.name,
						selected: false,
					}));
				}
			);
		},
		initPlekollectionList() {
			api.apiCall(
				"GET",
				"http://127.0.0.1:3000/plekollections/get_all",
				null,
				(e) => {
					let mongodata = JSON.parse(e);
					this.list_of_plekollections = mongodata;
				}
			);
		},
		getPlekje(name) {
			this.list_of_plekjes_names.forEach((e) => {
				e.selected = false;
			});
			for (let i = 0; i < this.list_of_all_plekjes.length; i++) {
				if (this.list_of_all_plekjes[i].name == name) {
					this.plekje = this.list_of_all_plekjes[i];
					this.list_of_plekjes_names[i].selected = true;
				}
			}
		},
		getPlekollection(name) {
			this.plekollection = this.list_of_plekollections.filter(
				(x) => x.name == name
			)[0];
		},
		showOnMap(fc) {
			if (fc) {
				this.$emit("showFeatureCollection", fc);
			} else {
				let singleFeatureFeatureCollection = {
					type: "FeatureCollection",
					features: [
						{
							type: "Feature",
							properties: {
								name: this.plekje.name,
							},
							geometry: this.plekje.geometry,
						},
					],
				};

				this.$emit(
					"showFeatureCollection",
					singleFeatureFeatureCollection
				);
			}
            this.$emit('unselectMenuButton')
		},
		showPlekollectionOnMap() {
			// for all ids in plekollection, find the plekje
			for (let i = 0; i < this.plekollection.plekjes.length; i++) {
				this.list_of_plekjes_to_display.push(
					this.list_of_all_plekjes.find(
						(e) => e._id == this.plekollection.plekjes[i].id // .id == ref id / ._id is own id
					)
				);
			}
			// turn this list of plekjes into feature collection
			let geojsonFeatureCollection = {
				type: "FeatureCollection",
				name: this.plekollection.name,
				features: [],
			};
			this.list_of_plekjes_to_display.forEach((e) =>
				geojsonFeatureCollection.features.push({
					type: "Feature",
					properties: {
						name: e.name,
					},
					geometry: e.geometry,
				})
			);
			// show on map
			this.showOnMap(geojsonFeatureCollection);
		},
	},
	mounted() {
		this.initPlekollectionList();
		this.initPlekjesList();
	},
};
</script>

<style scoped>
#offcanvasExampleLabel {
	text-align: left;
}
.btn {
	background-color: #17a2b8;
	color: #f2f2f2;
}
.plekjeslist div:hover {
	background-color: #ddd;
	color: black;
}
.plekjeslist div.selected {
	background-color: #ddd;
	color: black;
}
</style>