<template>
	<div class="topnav">
		<div v-for="item in navItems">
			<div v-if="item.name == 'Menu'">
				<a
					v-bind:class="{ active: this.showMenu }"
					@click="itemClicked(item.name)"
					role="button"
					data-bs-toggle="offcanvas"
					data-bs-target="#offcanvasExample"
					aria-controls="offcanvasExample"
				>
					{{ item.name }}
				</a>
			</div>
			<div v-else>
				<a
					v-bind:class="{ active: item.isActive }"
					@click="itemClicked(item.name)"
				>
					{{ item.name }}
				</a>
			</div>
		</div>
	</div>
</template>

<script>
export default {
	props: ["showMenu"],
	data: function () {
		return {
			navItems: [
				{ name: "Menu", isActive: false},
				{ name: "Map", isActive: true },
				//{ name: "Profile", isActive: false },
				//{ name: "Log in", isActive: false },
			],
			isActive: false,
		};
	},
	methods: {
		itemClicked(item_name) {
			this.navItems.forEach((e) => {
				if (e.name == item_name) {
					e.isActive = !e.isActive;
				}
			});
			if (item_name == "Menu") {
                    this.$emit("toggleMenu", !this.showMenu);
			}
			if (item_name == "Map") {
				this.$emit("toggleMap", this.navItems[1].isActive);
			}
		},
	},
    mounted(){
        //this.$emit("toggleMenu", !this.showMenu);
    }
};
</script>

<style scoped>
/* Add a black background color to the top navigation */
.topnav {
	background-color: #333;
	overflow: hidden;
}

/* Style the links inside the navigation bar */
.topnav a {
	float: left;
	color: #f2f2f2;
	text-align: center;
	padding: 10px 15px;
	text-decoration: none;
	font-size: 18.5px;
}

/* Change the color of links on hover */
.topnav a:hover {
	background-color: #ddd;
	color: black;
}

/* Add a color to the active/current link */
.topnav a.active {
	/* background-color: #60d1fa; */
    /* background-color: #0dcaf0; */
    /* background-color:#5bc0de ; */
    background-color:#17a2b8 ;
	color: white;
}
</style>